.head {
    font-size: 30px;
    text-align: center;
}

.form {
    text-align: center;
    padding: 30px 30px;
}

.form input {
    margin: 5px;
    width: 300px;
    height: 50px;
    text-align: center;
    font-size: 15px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 10px;
}

.form button {
    margin: 5px;
    padding: 10px;
    height: 55px;
    text-align: center;
    font-size: 15px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 10px;
    background-color: white;
    cursor: pointer;
}

.ul {
    display: table;
    margin-left: auto;
    margin-right: auto;
}

.btn_todo {
    background-color: rgb(255, 255, 255);
    border-radius: 25px;
    text-align: center;
    border: 1px solid rgb(0, 0, 0);
    cursor: pointer;
    margin-left: 20px;
}
